import { Injectable } from '@angular/core';
import { Http } from '@angular/http';

import 'rxjs/add/operator/toPromise';

import { JobVacancy } from './job-vacancy';

@Injectable()
export class JobVacancyService {

  private vacanciesUrl = 'jobs/billets.json';

  constructor(private http: Http) { }

  getVacancies(): Promise<JobVacancy[]> {
    return this.http.get(this.vacanciesUrl)
      .toPromise()
      .then(response => response.json().jobs as JobVacancy[])
      .catch(this.handleError);
  }

  getVacancy(id: string): Promise<JobVacancy> {
    return this.getVacancies()
      .then(vacancies => {
        for (let vacancy of vacancies) {
          if (vacancy.id == id) {
            return vacancy;
          }
        }
        return null;
      })
      .catch(this.handleError);



    // const url = `${this.vacanciesUrl}/${id}`;
    // return this.http.get(url)
    //   .toPromise()
    //   .then(response => response.json() as JobVacancy)
    //   .catch(this.handleError);
  }

  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error);
    return Promise.reject(error.message || error);
  }

}



