import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { Location } from '@angular/common';

import 'rxjs/add/operator/switchMap';

import { GetformService } from '../getform.service';
import { FormspreeService } from '../formspree.service';
import { GoogleAnalyticsService } from '../google-analytics.service';
import { JobVacancyService } from '../job-vacancy.service';
import { JobVacancy } from '../job-vacancy';

const EMAIL_REGEXP: RegExp = /^[a-z0-9!#$%&'*+\/=?^_`{|}~.-]+@[a-z0-9]([a-z0-9-]*[a-z0-9])?(\.[a-z0-9]([a-z0-9-]*[a-z0-9])?)*$/i;

@Component({
  selector: 'app-careers-opportunity',
  templateUrl: './careers-opportunity.component.html',
  styleUrls: ['./careers-opportunity.component.css']
})
export class CareersOpportunityComponent implements OnInit {

  vacancy: JobVacancy;

  window: Window = window; // for embedding the url to the vacancy

  referFromName: string = '';
  hasFromNameErrors: boolean = false;
  referFromEmail: string = '';
  hasFromEmailErrors: boolean = false;
  referToName: string = '';
  hasToNameErrors: boolean = false;
  referToEmail: string = '';
  hasToEmailErrors: boolean = false;
  isReferring: boolean = false;

  applyName: string = '';
  hasApplyNameErrors: boolean = false;
  applyEmail: string = '';
  hasApplyEmailErrors: boolean = false;
  applyResume: File = null;
  hasApplyResumeErrors: boolean = true;
  isApplying: boolean = false;

  constructor(
    private getform: GetformService,
    private formspree: FormspreeService,
    private ga: GoogleAnalyticsService,
    private jobVacancyService: JobVacancyService,
    private route: ActivatedRoute,
    private location: Location
  ) { }

  ngOnInit() {
    this.getVacancy();
  }

  getVacancy(): void {
    this.route.params.switchMap((params: Params) =>
      this.jobVacancyService.getVacancy(params['id'])).subscribe(vacancy =>
        this.vacancy = vacancy);
  }

  goBack(): void {
    this.location.back();
  }

  print(): void {
    this.ga.sendInteractiveEvent('Job Vacancy', 'print', this.vacancy.id);
    window.print();
  }

  email(): void { 
    this.ga.sendInteractiveEvent('Job Vacancy', 'email', this.vacancy.id);
    let url = `mailto:?subject=[TTG Solutions, Inc.] ${this.vacancy.title}`;
    url = `${url}&body=${this.vacancy.title}%0D%0A%0D%0A${window.location.href}`;
    window.location.href = url;
  }

  referStart(): void {
    this.isReferring = true;
  }

  referFinish(): void {
    if (!this.referValidate()) {
      return;
    }

    this.getform.send(GetformService.REFER_FORM_ID, {
      fromName: this.referFromName,
      fromEmail: this.referFromEmail,
      toName: this.referToName,
      toEmail: this.referToEmail,
      jobId: this.vacancy.id,
      jobTitle: this.vacancy.title,
      jobLocation: this.vacancy.location
    }).then(response => { }).catch(reason => {
      // getform.org doesn't allow custom redirects
      // and xhr does not allow redirect (because CORS)
      // so this will always throw an exception
      // and there isn't enough detail in the exception
      // to detect this single case,
      // so we must treat every error like a successful submission
      this.ga.sendInteractiveEvent('Job Vacancy', 'refer', this.vacancy.id);
      this.referClear(true);
    });
  }

  referClear(close: boolean = false): void {
    this.referFromName = this.referFromEmail = this.referToName = this.referToEmail = '';
    this.hasFromNameErrors = this.hasFromEmailErrors = this.hasToNameErrors = this.hasToEmailErrors = false;
    if (close) {
      this.isReferring = false;
    }
  }

  referValidate(): boolean {

    this.hasFromNameErrors = this.referFromName.length === 0;    
    this.hasFromEmailErrors = !this.validateEmail(this.referFromEmail);
    this.hasToNameErrors = this.referToName.length === 0;    
    this.hasToEmailErrors = !this.validateEmail(this.referToEmail);

    return !(this.hasFromNameErrors || this.hasFromEmailErrors || this.hasToNameErrors || this.hasToEmailErrors);
  }

  applyStart(): void {
    this.isApplying = true;
  }

  applyFinish(): void {
    if (!this.applyValidate()) {
      return;
    }
    
    this.getform.send(GetformService.APPLY_FORM_ID, {
      name: this.applyName,
      email: this.applyEmail,
      file: this.applyResume,
      jobId: this.vacancy.id,
      jobTitle: this.vacancy.title,
      jobLocation: this.vacancy.location
    }, true).then(response => { }).catch(reason => {
      // getform.org doesn't allow custom redirects
      // and xhr does not allow redirect (because CORS)
      // so this will always throw an exception
      // and there isn't enough detail in the exception
      // to detect this single case,
      // so we must treat every error like a successful submission
      this.ga.sendInteractiveEvent('Job Vacancy', 'apply', this.vacancy.id);
      this.applyClear(true);
    });
  }

  applyClear(close: boolean = false): void {

    this.hasApplyNameErrors = this.hasApplyEmailErrors = false;
    this.hasApplyResumeErrors = true;

    if (close) {
      this.isApplying = false;
    }
  }

  applyValidate(): boolean {

    this.hasApplyNameErrors = this.applyName.length === 0;    
    this.hasApplyEmailErrors = !this.validateEmail(this.applyEmail);

    return !(this.hasApplyNameErrors || this.hasApplyEmailErrors || this.hasApplyResumeErrors);
  }

  onApplyFile(event: Event) {
    console.log(event);
    let files: FileList = (<HTMLInputElement>event.srcElement).files;
    if (!files || files.length == 0) {
      this.hasApplyResumeErrors = true;
    } else {
      this.applyResume = files[0];
      this.hasApplyResumeErrors = false;
    }
  }

  validateEmail(email: string = ''): boolean {
    return email.length > 5 && EMAIL_REGEXP.test(email);
  }

}



