import { Component, OnInit } from '@angular/core';

import { GetformService } from '../getform.service';
import { GoogleAnalyticsService } from '../google-analytics.service';

const EMAIL_REGEXP: RegExp = /^[a-z0-9!#$%&'*+\/=?^_`{|}~.-]+@[a-z0-9]([a-z0-9-]*[a-z0-9])?(\.[a-z0-9]([a-z0-9-]*[a-z0-9])?)*$/i;

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {

  name: string = '';
  hasNameErrors: boolean = false;
  email: string = '';
  hasEmailErrors: boolean = false;
  subject: string = '';
  hasSubjectErrors: boolean = false;
  message: string = '';
  messageMax: number = 450;
  messageRemaining: number = 450;
  busy: boolean = false;
  submitted: boolean = false;
  
  constructor(
    private ga: GoogleAnalyticsService,
    private getform: GetformService
  ) { }

  ngOnInit() {
  }

  submit(): void {
    if (!this.validate()) {
      return;
    }

    this.busy = true;

    this.getform.send(GetformService.CONTACT_FORM_ID, {
      name: this.name,
      email: this.email,
      subject: this.subject,
      message: this.message
    }).then(response => {
        this.busy = false;
        this.submitted = true;
        this.clear();
      })
      .catch(error => {
        this.ga.sendInteractiveEvent('Contact', 'error', error.message);
        this.busy = false;
      });
  }

  validate(): boolean {
    this.hasNameErrors = this.name.length === 0;
    this.hasEmailErrors = !this.validateEmail(this.email);
    this.hasSubjectErrors = this.subject.length === 0;
    
    return !(this.hasNameErrors || this.hasEmailErrors || this.hasSubjectErrors);
  }

  clear(): void {
    this.name = this.email = this.subject = this.message = '';
    this.hasNameErrors = this.hasEmailErrors = this.hasSubjectErrors = false;
  }

  onMessageTyping(): void {
    this.messageRemaining = this.messageMax - this.message.length;
  }

  validateEmail(email: string): boolean {
    return email.length > 5 && EMAIL_REGEXP.test(email);
  }

}



