import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule, Routes } from '@angular/router';

// import { InMemoryWebApiModule } from 'angular-in-memory-web-api';
// import { InMemoryDataService }  from './in-memory-data.service';

import { FormspreeService } from './formspree.service';
import { GetformService } from './getform.service';
import { GoogleAnalyticsService } from './google-analytics.service';
import { JobVacancyService } from './job-vacancy.service';

import { AboutComponent } from './about/about.component';
import { AppComponent } from './app.component';
import { CareersBenefitsComponent } from './careers-benefits/careers-benefits.component';
import { CareersOpportunitiesComponent } from './careers-opportunities/careers-opportunities.component';
import { CareersOpportunityComponent } from './careers-opportunity/careers-opportunity.component';
import { ContactComponent } from './contact/contact.component';
import { FooterComponent } from './footer/footer.component';
import { GalleryHeroComponent } from './gallery-hero/gallery-hero.component';
import { HeaderComponent } from './header/header.component';

const routes: Routes = [
  {
    path: '',
    component: GalleryHeroComponent
  },
  {
    path: 'benefits',
    component: CareersBenefitsComponent
  },
  {
    path: 'careers',
    component: CareersOpportunitiesComponent
  },
  {
    path: 'careers/:id',
    component: CareersOpportunityComponent
  },
  {
    path: 'contact',
    component: ContactComponent
  },
  {
    path: '**',
    redirectTo: ''
  }
];

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    GalleryHeroComponent,
    AboutComponent,
    ContactComponent,
    FooterComponent,
    CareersOpportunityComponent,
    CareersBenefitsComponent,
    CareersOpportunitiesComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    // InMemoryWebApiModule.forRoot(InMemoryDataService, {
    //   passThruUnknownUrl: true
    // }),
    RouterModule.forRoot(routes, {
      useHash: true
    })
  ],
  providers: [
    GetformService,
    FormspreeService,
    GoogleAnalyticsService,
    JobVacancyService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



