import { Injectable } from '@angular/core';

@Injectable()
export class GoogleAnalyticsService {

  private ga?: any;

  constructor() {
    this.ga = window.hasOwnProperty('ga') ? window['ga'] : null;
  }

  sendInteractiveEvent(eventCategory: string, eventAction: string, eventLabel?: string, eventValue?: number): void {
    try {
      this.ga('send', {
        hitType: 'event',
        eventCategory: eventCategory,
        eventAction: eventAction,
        eventLabel: eventLabel
      });
    } catch (e) {
      console.error(e);
    }
  }

  sendPageView(page: string): void {
    if (!page.startsWith('/')) {
      page = '/' + page;
    }
    try {
      this.ga('send', {
        hitType: 'pageView',
        page: page
      });
    } catch (e) {
      console.error(e);
    }
  }
}



