import { Injectable } from '@angular/core';
import { Http, Headers, Response, URLSearchParams } from '@angular/http';

import 'rxjs/add/operator/toPromise';

@Injectable()
export class GetformService {

  static REFER_FORM_ID: string = 'e22513c0-f09f-4ea7-964b-c0de1fc05f2e';
  static APPLY_FORM_ID: string = 'addcca17-a306-490c-9936-e90d9e95db55';
  static CONTACT_FORM_ID: string = '6cd816a1-fc60-49bc-9a8b-795f63a5c834';

  constructor(
    private http: Http
  ) { }
  
  send(formId: string, params: Object, hasFile: boolean = false): Promise<Response> {
    
    let data: any = hasFile ? new FormData() : new URLSearchParams();
    for (let param in params) {
      if (params.hasOwnProperty(param)) {
        data.append(param, params[param]);
      }
    }

    let pathChoice = hasFile ? 'u' : 'f';

    return this.http.post(`https://getform.org/${pathChoice}/${formId}`, data).toPromise();
  }

}



