import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { JobVacancyService } from '../job-vacancy.service';
import { JobVacancy } from '../job-vacancy';

@Component({
  selector: 'app-careers-opportunities',
  templateUrl: './careers-opportunities.component.html',
  styleUrls: ['./careers-opportunities.component.css']
})
export class CareersOpportunitiesComponent implements OnInit {

  vacancies: JobVacancy[] = [];

  constructor(
    private jobVacancyService: JobVacancyService,
    private router: Router
  ) { }

  ngOnInit() {
    this.getVacancies();
  }

  getVacancies(): void {
    this.jobVacancyService.getVacancies().then(vacancies => {
      vacancies.sort((a, b) => {
        return a.title.localeCompare(b.title);
      });
      this.vacancies = vacancies;
    });
  }

}



